<template><section>

<h1>Icon 图标</h1>
<hr>
<p>语义化的矢量图形，<code v-pre="" class="code_inline">xview</code> 使用开源的 Iconfont (阿里妈妈MUX倾力打造的矢量图标管理、交流平台) 作为图标库，并制作成了 <code v-pre="" class="code_inline">icon font</code>。</p>
<h3>如何使用</h3>
<p>使用 <code v-pre="" class="code_inline">class=&quot;icon-XXX&quot;</code> 来声明图标，具体图标的名称请 <code v-pre="" class="code_inline">copy</code> 相应的标签</p>
<div class="demo-block">
  <i class="icon-alarm fs-24"></i>
  <i class="icon-android fs-24"></i>
  <i class="icon-apple fs-24"></i>
  <i class="icon-cog fs-24"></i>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body><i class="icon-alarm fs-24"></i>
  <i class="icon-android fs-24"></i>
  <i class="icon-apple fs-24"></i>
  <i class="icon-cog fs-24"></i>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"icon-alarm fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"icon-android fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"icon-apple fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"icon-cog fs-24"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>图标示例</h3>
<ul class="iconfont-list">
  <li v-for="name in icons" :key="name">
    <span>
      <i :class="name"></i>
      {{name}}
    </span>
  </li>
</ul>
</section></template>
<style lang="scss">
  .demo-icon .source > i {
    font-size: 24px;
    color: #8492a6;
    margin: 0 20px;
    font-size: 1.5em;
    vertical-align: middle;
  }
  
  .demo-icon .source > button {
    margin: 0 20px;
  }

  .iconfont-list {
    overflow: hidden;
    list-style: none;
    padding: 0;
    border: solid 1px #eaeefb;
    border-radius: 4px;
  }
  .iconfont-list li {
    float: left;
    width: 16.66%;
    text-align: center;
    height: 120px;
    line-height: 120px;
    color: #666;
    font-size: 13px;
    transition: all .3s;
    cursor: pointer;

    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-right: -1px;
    margin-bottom: -1px;
    span {
      display: inline-block;
      line-height: normal;
      vertical-align: middle;
      font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
    }
    i {
      display: block;
      font-size: 32px;
      margin-bottom: 15px;
      transition: all .2s;
    }
    &:hover{
    	color: rgb(80,80,80);
    	box-shadow:0px 2px 8px 0px rgba(0,0,0,0.1);
	    i{
	      font-size: 36px;
	    }
    } 
  }
</style>
<script>
  var iconList = require('../icon.json');

  export default {
    data() {
      return {
        icons: iconList
      };
    }
  }
</script>


