<h3>自定义关闭按钮</h3>
<p>自定义关闭按钮为文字或其他符号。</p>
<div class="dome-alert demo-block">
  <w-alert type="success" :closable="false">不可关闭的 alert</w-alert>
  <w-alert type="info" close-text="知道了">自定义关闭的 alert</w-alert>
  <w-alert type="warning" @close="hello">设置了回调的 alert</w-alert>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body><div class="dome-alert demo-block">
  <w-alert type="success" :closable="false">不可关闭的 alert</w-alert>
  <w-alert type="info" close-text="知道了">自定义关闭的 alert</w-alert>
  <w-alert type="warning" @close="hello">设置了回调的 alert</w-alert>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dome-alert demo-block"</span>&gt;</span>
