<template><section><h1>MetaInfo 三要素管理</h1>
<hr>
<p><code v-pre="" class="code_inline">vue-meta-info</code> 是一个基于<a href="https://vuejs.org">vue 2.0</a>的插件，它会让你更好的管理你的 app 里面的 meta 信息。你可以直接
在组件内设置 metaInfo 便可以自动挂载到你的页面中。如果你需要随着数据的变化，自动更新你的title、meta等信息，那么用此
插件也是再合适不过了。
当然，有时候我们也可能会遇到让人头疼的SEO问题，那么使用此插件配合 <a href="https://github.com/chrisvfritz/prerender-spa-plugin">prerender-spa-plugin</a> 也是再合适不过了。</p>
<blockquote>
<p>更多使用介绍：<a href="https://zhuanlan.zhihu.com/p/29148760?group_id=890298677627879424">处理seo的另一种思路</a></p>
</blockquote>
<h3>常规用法</h3>
<div class="demo-block">
  <p>打开控制台，看看这个页面的 title, meta, 和 link 哦，是不是发生了变化</p>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body>export default {
    metaInfo: {
      title: 'My Example App',
      meta: [{                
        name: 'keyWords',
        content: 'My Example App'
      }],
      link: [{              
        rel: 'asstes',
        href: 'https://xview.github.io/xview/'
      }]
    }
  }
  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-js">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">metaInfo</span>: {
      <span class="hljs-attr">title</span>: <span class="hljs-string">'My Example App'</span>,
      <span class="hljs-attr">meta</span>: [{                
        <span class="hljs-attr">name</span>: <span class="hljs-string">'keyWords'</span>,
        <span class="hljs-attr">content</span>: <span class="hljs-string">'My Example App'</span>
      }],
      <span class="hljs-attr">link</span>: [{              
        <span class="hljs-attr">rel</span>: <span class="hljs-string">'asstes'</span>,
        <span class="hljs-attr">href</span>: <span class="hljs-string">'https://xview.github.io/xview/'</span>
      }]
    }
  }
  
</code></pre>
</div></demo-block>
<h3>async 异步加载</h3>
<div class="demo-block">
  <p>如果你的title或者meta或者link是异步加载的，那么你可能需要这样使用</p>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body>export default {
    name: 'async',
    metaInfo () {
      return {
        title: this.pageName
      }
    },
    data () {
      return {
        pageName: 'loading'
      }
    },
    mounted () {
      setTimeout(() => {
        this.pageName = 'async'
      }, 2000)
    }
  }
  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-js">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">name</span>: <span class="hljs-string">'async'</span>,
    metaInfo () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">title</span>: <span class="hljs-keyword">this</span>.pageName
      }
    },
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">pageName</span>: <span class="hljs-string">'loading'</span>
      }
    },
    mounted () {
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        <span class="hljs-keyword">this</span>.pageName = <span class="hljs-string">'async'</span>
      }, <span class="hljs-number">2000</span>)
    }
  }
  
</code></pre>
</div></demo-block>

</section></template>

<script>
  export default {
    metaInfo: {
      title: 'metaInfo', // set a title
      meta: [{                 // set meta
        name: 'keyWords',
        content: 'metaInfo'
      }],
      link: [{                 // set link
        rel: 'asstes',
        href: 'https://xview.github.io/xview/'
      }]
    }
  }
</script>


