<template><section><h1>Limit 限制输入域</h1>
<hr>
<h3>基础用法</h3>
<p>Limit 可以控制用户输入的字符数，<code v-pre="" class="code_inline">placeholder</code> 指定初始输入的提示文案</p>
<div class="demo-block">
<x-limit :maxLen="10" placeholder="最多输入10个字符"></x-limit>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body><x-limit v-mode="content" :max-len="10" placeholder="最多输入10个字符"></x-limit>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">x-limit</span> <span class="hljs-attr">v-mode</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">:max-len</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最多输入10个字符"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">x-limit</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>裁剪超出字符</h3>
<p>如果超过了限制的字符数，超过部分进行裁剪</p>
<div class="demo-block">
<x-limit :max-len="10" placeholder="最多输入10个字符" isCut=""></x-limit>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body><x-limit v-mode="content" :max-len="10" placeholder="最多输入10个字符" is-cut=""></x-limit>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">x-limit</span> <span class="hljs-attr">v-mode</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">:max-len</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最多输入10个字符"</span> <span class="hljs-attr">is-cut</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">x-limit</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>列高</h3>
<p>通过 <code v-pre="" class="code_inline">rows</code> 属性指定<code v-pre="" class="code_inline">limit</code>的高度</p>
<div class="demo-block">
<x-limit :max-len="10" :rows="3" placeholder="最多输入10个字符" is-cut=""></x-limit>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body><x-limit :max-len="10" :rows="3" placeholder="最多输入10个字符" is-cut=""></x-limit>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">x-limit</span> <span class="hljs-attr">:max-len</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最多输入10个字符"</span> <span class="hljs-attr">is-cut</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">x-limit</span>&gt;</span>

</code></pre>
</div></demo-block>

<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>rows</td>
<td>列高</td>
<td>Number</td>
<td>—</td>
<td>4</td>
</tr>
<tr>
<td>max-len</td>
<td>Number</td>
<td>最大长度限制</td>
<td>-</td>
<td>20</td>
</tr>
<tr>
<td>is-cut</td>
<td>超出字符是否裁剪</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入时需要显示的提示文案</td>
<td>String</td>
<td>—</td>
<td>请输入内容</td>
</tr>
</tbody>
</table>
<h2>Limit 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>overText</td>
<td>超出限制长度触发</td>
<td>content</td>
</tr>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>value</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      content: ''
    };
  }
}
</script>


