<template><section><h1>Shade 遮罩|蒙版|浮层</h1>
<hr>
<p>用于页面中展示重要的提示信息或弹层的容器。</p>
<h3>基本用法</h3>
<p>页面中的蒙版,挂载在body上,全局固定定位,宽高为100%。</p>
<div class="demo-block">
	<x-button @click.native="showShade" type="primary" radius="">点击显示遮罩层</x-button>
</div>

<demo-block class="111">
                        <div slot="desc"><html><head></head><body><div>
<x-button @click.native="showShade" type="primary" radius="">点击显示遮罩层</x-button>
</div>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click.native</span>=<span class="hljs-string">"showShade"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">radius</span>&gt;</span>点击显示遮罩层<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
  mounted () {},
  <span class="hljs-attr">methods</span>: {
    showShade() {
      <span class="hljs-keyword">var</span> _this = <span class="hljs-keyword">this</span>;
      _this.$shade.show();
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span>{
        _this.$shade.hide();
      },<span class="hljs-number">1000</span>)
    }
  },    
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题，必选参数。也可通过默认 slot 传入</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/error</td>
<td>info</td>
</tr>
<tr>
<td>description</td>
<td>辅助性文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>center</td>
<td>文字是否居中</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>close-text</td>
<td>关闭按钮自定义文本</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>show-icon</td>
<td>是否显示图标</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>关闭alert时触发的事件</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    mounted () {
    },
    methods: {
	  	showShade() {
      	var _this = this;
        _this.$shade.show();
        setTimeout(()=>{
        	_this.$shade.hide();
        },1000)
	  	}
    },    
  }
</script>


