<template><section><h1>LoadingBar 加载进度条</h1>
<hr>
<p>全局创建了一个用于显示页面加载、异步请求的加载进度条。
因为可复用性的关系，<code v-pre="" class="code_inline">LoadingBar</code> 只会全局创建一个实例，而且在 <code v-pre="" class="code_inline">Vue.prototype</code> 中添加了全局对象 <code v-pre="" class="code_inline">$Loading</code>，可以直接通过 <code v-pre="" class="code_inline">this.$Loading</code> 操作实例</p>
<h3>基础用法</h3>
<p>通过调用 $Loading 提供的三种方法来控制全局的加载进度条 <code v-pre="" class="code_inline">start()</code>、<code v-pre="" class="code_inline">end()</code>、<code v-pre="" class="code_inline">error()</code></p>
<div class="demo-block">
	<x-button @click.native="start" type="success">开始</x-button>
	<x-button @click.native="end" type="primary">结束</x-button>
	<x-button @click.native="error" type="danger">中断</x-button>
</div>

<demo-block class="111">
                        <div slot="desc"><html><head></head><body><x-button @click.native="start" type="success">开始</x-button>
<x-button @click.native="end" type="primary">结束</x-button>
<x-button @click.native="error" type="danger">中断</x-button>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click.native</span>=<span class="hljs-string">"start"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>开始<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click.native</span>=<span class="hljs-string">"end"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>结束<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click.native</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span>&gt;</span>中断<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    <span class="hljs-attr">methods</span>: {
      start () {
        <span class="hljs-keyword">this</span>.$loading.start()
      },
      end () {
        <span class="hljs-keyword">this</span>.$loading.end()
      },
      error () {
        <span class="hljs-keyword">this</span>.$loading.error()
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>常规配置</h3>
<p>提供 LoadingBar 的全局配置，使用方法如下：</p>
<div class="demo-block">
   <x-button @click.native="setSpeed" type="primary">设置speed速度</x-button>
   <x-button @click.native="setSpinner" type="primary">设置spinner动画效果</x-button>
   <x-button @click.native="setPercentNum" type="primary">设置percentNum每次加载的比例</x-button>
   <x-button @click.native="setShowSpinner" type="primary">设置是否显示spinner</x-button>
</div>
<demo-block class="111">
                        <div slot="desc"><html><head></head><body><x-button @click="setSpeed" type="primary">设置speed速度</x-button>
<x-button @click="setSpinner" type="primary">设置spinner动画效果</x-button>
<x-button @click="setPercentNum" type="primary">设置percentNum每次加载的比例</x-button>
<x-button @click="setShowSpinner" type="primary">设置是否显示spinner</x-button>


</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setSpeed"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>设置speed速度<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setSpinner"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>设置spinner动画效果<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setPercentNum"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>设置percentNum每次加载的比例<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">x-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"setShowSpinner"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>设置是否显示spinner<span class="hljs-tag">&lt;/<span class="hljs-name">x-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    <span class="hljs-attr">methods</span>: {
     setSpeed () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">speed</span>: <span class="hljs-number">10</span>
       })
       <span class="hljs-keyword">this</span>.$loading.start()
     },
     setSpinner () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">easing</span>: <span class="hljs-string">'ease'</span>
       })
       <span class="hljs-keyword">this</span>.$loading.start()
     },
     setPercentNum () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">percentNum</span>: <span class="hljs-number">0.1</span>
       })
       <span class="hljs-keyword">this</span>.$loading.start()
     },
     setShowSpinner () {
       <span class="hljs-keyword">this</span>.$loading.config({
         <span class="hljs-attr">showSpinner</span>: <span class="hljs-literal">false</span>
       })
       <span class="hljs-keyword">this</span>.$loading.start()
     }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>LoadingBar config参数</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>speed</td>
<td>加载速度</td>
<td>Number</td>
<td>0-100</td>
<td>5</td>
</tr>
<tr>
<td>easing</td>
<td>spinner加载动画</td>
<td>String</td>
<td>linear, ease, cubic-bezier...</td>
<td>贝萨尔曲线值</td>
</tr>
<tr>
<td>percentNum</td>
<td>每次前进的百分比</td>
<td>Float</td>
<td>0-1</td>
<td>Math.random()</td>
</tr>
<tr>
<td>showSpinner</td>
<td>是否显示spinner</td>
<td>Boolean</td>
<td>true, false</td>
<td>是否显示spinner</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    mounted () {
    
    },
    methods: {
      start () {
        this.$loading.start()
      },
      end () {
        this.$loading.end()
      },
      error () {
        this.$loading.error()
      },
      setSpeed () {
        this.$loading.config({
          speed: 10
        })
        this.$loading.start()
      },
      setSpinner () {
        this.$loading.config({
          easing: 'ease'
        })
        this.$loading.start()
      },
      setPercentNum () {
        this.$loading.config({
          percentNum: 0.1
        })
        this.$loading.start()
      },
      setShowSpinner () {
        this.$loading.config({
          showSpinner: false
        })
        this.$loading.start()
      }
    }
  }
</script>


